.form-container {
	margin: 20px auto;
	width: 80%;
	box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.2);
	position: relative;
	border-radius: 10px;
	height: 600px;
	display: grid;
	grid-template-columns: 1fr 1fr;
	flex-direction: column;
	flex-flow: column;
}

.close-btn {
	position: absolute;
	top: 2%;
	right: 1%;
	font-size: 1.5rem;
	z-index: 1;
	cursor: pointer;
}

.fa-times {
	font-size: 1.5rem;
	margin-right: 0.6rem;
	color: rgb(27, 56, 75);
}

.form-content-left {
	background: linear-gradient(
		90deg,
		rgb(209, 224, 247) 0%,
		rgb(216, 222, 252) 100%
	);
	border-radius: 10px 0 0 10px;
	position: relative;
}

.form-img {
	width: 80%;
	height: 80%;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.form-img-2 {
	width: 50%;
	height: 50%;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.form-success {
	text-align: center;
	font-size: 24px;
	margin-top: 80px;
	color: #fff;
}

.form-content-right {
	border-radius: 0 10px 10px 0;
	position: relative;
	background: linear-gradient(
		90deg,
		rgb(125, 163, 187) 0%,
		rgb(107, 146, 170) 100%
	);
}

.form {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 90%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.form h1 {
	font-size: 1rem;
	text-align: start;
	width: 80%;
	margin-bottom: 1rem;
	color: rgb(21, 47, 65);
}

.form-inputs {
	margin-bottom: 0.5rem;
	width: 80%;
}

.form-inputs p {
	font-size: 0.8rem;
	margin-top: 0.5rem;
	color: #f00e0e;
}

.form-label {
	display: inline-block;
	font-size: 0.8rem;
	margin-bottom: 6px;
	color: rgb(24, 49, 66);
}

.form-input {
	display: block;
	padding-left: 10px;
	outline: none;
	border-radius: 2px;
	height: 40px;
	width: 100%;
	border: none;
}

.form-input::placeholder {
	color: #595959;
	font-size: 12px;
}

.form-input-btn {
	width: 80%;
	height: 50px;
	margin-top: 10px;
	border-radius: 2px;
	background: linear-gradient(
		90deg,
		rgb(84, 99, 117) 0%,
		rgb(65, 73, 105) 100%
	);
	outline: none;
	border: none;
	color: #fff;
	font-size: 1rem;
}

.form-input-btn:hover {
	cursor: pointer;
	background: linear-gradient(
		90deg,
		rgb(44, 84, 133) 0%,
		rgb(45, 66, 148) 100%
	);
	transition: all 0.4s ease-out;
}

.form-login-btn {
	width: 80%;
	height: 50px;
	margin-top: 10px;
	border-radius: 2px;
	background: linear-gradient(
		90deg,
		rgb(84, 99, 117) 0%,
		rgb(65, 73, 105) 100%
	);
	outline: none;
	border: none;
	color: #fff;
	font-size: 1rem;
}

.form-login-btn:hover {
	cursor: pointer;
	background: linear-gradient(
		90deg,
		rgb(44, 84, 133) 0%,
		rgb(45, 66, 148) 100%
	);
	transition: all 0.4s ease-out;
}

.form-input-login {
	font-size: 0.8rem;
	margin-top: 10px;
	color: #fff;
	width: 80%;
	text-align: center;
}

.form-input-login a {
	text-decoration: none;
	color: #364e81;
	font-weight: 600;
}

.form-input-signup {
	font-size: 0.8rem;
	margin-top: 10px;
	color: #fff;
	width: 80%;
	text-align: center;
}

.form-input-signup a {
	text-decoration: none;
	color: #364e81;
	font-weight: 600;
}

/* Dont display image & change the form size when being resized */
@media only screen and (max-width: 960px) {
	.form-content-left {
		display: none;
	}

	.form-container {
		grid-template-columns: 1fr;
		width: 80%;
	}

	.form-content-right {
		border-radius: 10px;
	}

	.close-btn {
		color: rgb(35, 71, 95);
		font-size: 1.5rem;
	}
}
